.taskbar {
    background-color: #27231F;
    color:#fff;
    height:60px;
    display: flex;
    flex-direction: row;
}

.taskbar__start {
    width:72px;
}

.taskbar__tasklist {
    flex: 1;
    display: flex;
    flex-direction: row;
}

.taskbar__tasklist__icon {
    width:72px;
    box-sizing: border-box;
    padding:10px;
    border-bottom:5px solid #429CE3;
}

.taskbar__tasklist__icon--current {
    background-color: #514D4B;
}

.taskbar__tasklist__icon:hover {
    background-color:#63605E;
}

.taskbar__clock {
    padding-left:10px;
    padding-right:10px;
    text-align:center;
    display: flex;
    flex-direction: column;
}

.taskbar__clock__time, .taskbar__clock__date {
    flex: 1;
}