#starticon {
    fill-rule:evenodd;
    clip-rule:evenodd;
    stroke-linejoin:round;
    stroke-miterlimit:2;
}
#starticon #background, #starticon.starticon--disabled:hover #background {
    fill:rgb(39,35,31);
}
#starticon:hover #background {
    fill:#3D3934;
}
#starticon #logo, #starticon.starticon--disabled:hover #logo {
    fill:white;
}
#starticon:hover #logo {
    fill:#429CE3;
}